.item {
  display: flex;
  justify-content: space-between;
  font-size: 24px;
  width: 100%;
  margin-bottom: 15px;
  min-height: 48px;
}

.item__text {
   background-color: #EEE7ED;
   color: #332525;
   padding: 8px 16px;
   border-radius: 7px;
   width: 100%;
   min-width: 120px;
   margin-right: 10px;
   line-height: 30px;
}

.item__btn {
  background-color:#FF4D4D;
}